.App-header {
  background-color: #493c3496;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}

.App-link {
  color: #ffffff00;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.root{
    text-align: center;
    background-image: url(/static/media/back.8a43d639.jpg);
    background-position: center;
    background-size: cover;
    width: 100vw;
    background-repeat: repeat;
    background-attachment: fixed;
}

  .container{
      display: inline-block;
      text-align: center;
      margin: 3vw 3vw 5vh 3vw;
      border: 1px 1px 1px black;
      border-radius: 10px;
      -webkit-backdrop-filter: blur(17px);
      backdrop-filter: blur(10px);
      box-shadow: 0 5px 20px rgba(77, 77, 77, 0.76);
      align-items: center;
      justify-content: space-between;
  }

  .smallTitle{
      display:flex;
      width: 80vw;
      height: 60px;
      max-width: 550px;
      padding: 10px 10px 0px 10px;
      background-color: #979090;
      border-radius: 10px 10px 0px 0px;
      opacity: 0.8;
      font-size: 23px;
      color: rgb(46, 35, 35);
  }

  .topic{
      display:flex;
      width: 80vw;
      max-width: 550px;
      height: 200px;
      padding: 10px;
      border-radius: 0px 0px 10px 10px;
      background-color: rgba(97, 97, 97, 0.089);
  }

  .explain{
      margin-left: 20px;
      margin-right: 20px;
      height: 130px;
  }

.title{
    position: relative;
    display: inline-block;
    font-size: 35px;
    padding-bottom: 5px;
    animation-name: fade;
    animation-duration: 3s;
    animation-iteration-count: 1;
    margin-top: calc(40vh - 130.4px);
    font-family: 'Raleway', sans-serif;
  }
  
  .title::after{
    position: absolute;
    top: -2px;
    right: -2px;
    bottom: -5px;
    left: -2px;
    content: '';
    animation-name: underline;
    animation-duration: 3s;
    animation-delay: 0s;
    transition-duration: 3s;
    border-bottom: 3px solid rgba(148, 131, 131, 0.664);
    transform: scale(0.8, 1);
  }

  .content{
    font-size: 15px;
    margin-left: 15vw;
    margin-right: 15vw;
    width: 70vw;
    text-align: center;
    align-items: center;
    font-family: 'M PLUS 1p', sans-serif;
    animation: fade2;
    animation-duration: 5s;
    animation-iteration-count: 1;
  }

  @keyframes underline{
    0%{
      border-bottom: 3px solid rgba(148, 131, 131, 0.664);
      transform: scale(0, 1);
    }
    30%{
      border-bottom: 3px solid rgba(148, 131, 131, 0.664);
      transform: scale(0, 1);
    }
    100%{
      border-bottom: 3px solid rgba(148, 131, 131, 0.664);
      transform: scale(0.8, 1);
    }
  }

  .subtitle{
    font-family: 'Raleway', sans-serif;
    font-size: 40px;
    margin-top: 45vh;
  }


  /* .title::before{
